.item-count-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.count-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 190px;
  height: 30px;
  border: 2px solid grey;
  border-radius: 10px;
}
.button-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 27px;
  width: 27px;
  font-size: 1.2rem;
  border-radius: 30px;
  background: #fff;
  border: 2px solid #918f8f;
  cursor: pointer;
}

.button-container[disabled] {
  color: grey;
  border: 1px solid #d1d1d1;
}
.button-cart {
  width: 180px;
  height: 40px;
  margin: 5px;
  font-size: 18px;
  line-height: 20px;
  border-radius: 15px;
  border: none;
  cursor: pointer;
  color: #fff;
  background-color: #d3145a;
}

@media (min-width: 1024px) {
  .count-container {
    width: 150px;
    height: 50px;
  }

  .button-container {
    height: 30px;
    width: 30px;
    font-size: 20px;
    line-height: 20px;
    border-radius: 15px;
  }

  .button-cart {
    width: 150px;
    height: 50px;
  }
}
